$primary-color: "#ffff";
$secondary-color: #f39c12;
$text-color: "#2c3e50";
// import fonts
@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@400;500;600;700&family=Lato&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
// import fonts

@mixin getCurrentColorScheme($dark-theme: true){
    @if($dark-theme){
        $primary-color: #212529 !global;
        $text-color: #ecf0f1 !global;
        background-color: $primary-color;
        color:$text-color;

    }
    @else{
        $primary-color: #ffffff !global;
        $text-color: "#2c3e50" !global;

        background-color: $primary-color;
        color:$text-color;

        .navbar-dark{
            background-color:white !important;
        }
        .navbar-dark *{
            color:#212529 !important;
        }
    }
}



.MainPage.light{
    @include getCurrentColorScheme($dark-theme: false);    
    
}

.MainPage.dark{
    @include getCurrentColorScheme($dark-theme: true);
}

.MainPage{

    @warn $primary-color;
    font-family: 'Roboto', sans-serif !important;
   
}

.site-logo{
    width:120px;
}

.codingeagle_scores{
    width:100%;
    overflow:hidden;display:block;
}

@media only screen and (max-width:756px){
    .d-lg-block{
        display:none !important;
    }
}



.navigateButton{
    border:none !important;
    outline:none !important;
    background:none !important;
}

.navigateButton h5{
    font-size:0.8rem;
    margin-top:0.5rem;
    font-weight:normal;
    font-family: 'Lato', sans-serif;
    text-align:center;
}

.navigateButton:hover svg,.navigateButton:hover h5,.active_widget{
    fill: #f10019 !important;
    color: #f10019 !important;
}


@media only screen and (max-width:756px){
    .live_embed_area{
        min-height:450vh !important;
    }
}
.live_embed_area{
    min-height:800vh;
}

.coding_eagle_news{
    background-color:#f3f3f3;
    padding:20px;
}